function get_models() {
    let left = 0, top = 0, width = 0, height = 0;
    let models = [
        {
            left, top, width, height,
            colspan: 8,
            rowspan: 3,
            innerHTML:
                `
    
<code>
{
    //
    ncols: 16,
    //0
    space: { left: 2, right: 2, top: 4, bottom: 0 }, 
    //0
    capture: { top: 0, right: 0, bottom: 0, left: 0 }
}
</code>
    `
        },
        {
            left, top, width, height,
            col: 9,
            row: 0,
            colspan: 7,
            rowspan: 3,
            innerHTML:
                `
                <br><button style="color:white;background-color:black;"></button>
<code>
{
    col: 9,
    row: 0,
    colspan: 7,
    rowspan: 4,
    capture: (e, t) => t.target.tagName === "BUTTON"
}
</code>
    `,
            capture: (e, t) => t.target.tagName === "BUTTON"
        },
        {
            left, top, width, height,
            colspan: 4,
            rowspan: 7,
            innerHTML:
                `
                <span style="
                display:block;
                width:0;height:0;
                z-index: 1000;
                border: 20px solid;
                border-color: aqua blue red green;
                "></span>
                <br>
                <br>
                (1):<br>
                (2): 10px <br>
                (3): 10px <br>
                (3): 10px <br>
<code>
{
    colspan: 4,
    rowspan: 6,
    capture: {
        top: 0,
        right: -10,
        bottom: 10,
        left: -10
    }
}
</code>
    `,
            capture: {
                top: 0,
                right: -10,
                bottom: 10,
                left: -10,
            }
        },
        {
            left, top, width, height,
            colspan: 4,
            rowspan: 4,
            style: {
                border: "solid black",
                borderWidth: "10px 20px"
            },
            innerHTML:
                `
    
<code>
{
    colspan: 4,
    rowspan: 4,
    capture: {
        top: -10,
        right: -20,
        bottom: -10,
        left: -20
    }
}
</code>
    `,
            capture: {
                top: -10,
                right: -20,
                bottom: -10,
                left: -20
            },
        },
        {
            left, top, width, height,
            colspan: 4,
            rowspan: 5,
            innerHTML:
                `
                :<br>
                (1) 20px , 30px <br>
                (2) space-right space-left  2 
<code>
{
    colspan: 4,
    rowspan: 5,
    space: {
        top: 20,
        right: -2,
        bottom: 30,
        left: -2
    }
}
</code>
    `,
            space: {
                top: 20,
                right: -2,
                bottom: 30,
                left: -2
            },
        },
        {
            left, top, width, height,
            col:8,
            row:0,
            colspan: 4,
            rowspan: 3,
            innerHTML:
                `
                 "col" "row" 
<code>
{
    col:8,
    row:0,
    colspan: 4,
    rowspan: 3
}
</code>
    `,
        },
    ];
    return models;
}
function get_option() {
    return {
        ncols: Math.min(window.innerWidth / 70 | 0, 16),
        space: { left: 2, right: 2, top: 4, bottom: 0 },
        capture: { top: 0, right: 0, bottom: 0, left: 0 }
    };
}
export { get_models, get_option };